.down-bg {
    @media screen and (min-width: 1280px) {
        margin-left: auto;
        margin-right: auto;
        max-width: 1600px;
        background-image: url("../images/desctop/bg-body@1x.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
            background-image: url("../images/desctop/bg-body@2x.jpg");
        }
    }
    @media screen and (min-width: 768px) {
        margin-left: auto;
        margin-right: auto;
        background-image: url("../images/tablet/tablets-bg-body@1x.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
            background-image: url("../images/tablet/tablets-bg-body@2x.jpg");
        }
    }
    @media screen and (min-width: 320px) {
        margin-left: auto;
        margin-right: auto;
        width: 100%;
        background-image: url("../images/mobile/mobile-bg-body@1x.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
            background-image: url("../images/mobile/mobile-bg-body@2x.jpg");
        }
    }
}